import React from 'react';
import { Building } from '../../game/buildings/types';
import { calculateBuildingCost } from '../../game/buildings/calculations';

interface BuildingCardProps {
  building: Building;
  onPurchase: () => void;
  canAfford: boolean;
  isFirstPurchase?: boolean;
}

const BuildingCard: React.FC<BuildingCardProps> = ({
  building,
  onPurchase,
  canAfford,
  isFirstPurchase = false
}) => {
  const { name, description, icon, count, baseProduction } = building;
  const cost = calculateBuildingCost(building);

  // Format numbers to whole numbers only
  const formattedCost = Math.floor(cost);
  const formattedProduction = Math.floor(baseProduction * count);

  return (
    <div className={`relative bg-white p-4 rounded-lg shadow-md border-2 transition-all duration-300 ${
      isFirstPurchase && canAfford ? 'border-yellow-400 hover:border-yellow-500' :
      canAfford ? 'border-green-400 hover:border-green-500' : 'border-gray-300'
    }`}>
      {isFirstPurchase && canAfford && (
        <div className="absolute -top-2 -right-2 bg-yellow-400 text-xs text-yellow-900 px-2 py-1 rounded-full font-bold">
          Recommended
        </div>
      )}

      <div className="flex items-center mb-2">
        <div className="text-3xl mr-2">{icon}</div>
        <div>
          <h3 className="font-bold text-lg">{name}</h3>
          <p className="text-sm text-gray-600">{description}</p>
        </div>
      </div>

      <div className="mt-3 flex justify-between items-center">
        <div>
          <div className="text-sm text-gray-600">Owned: <span className="font-semibold">{count}</span></div>
          <div className="text-sm text-gray-600">
            Produces: <span className="font-semibold text-blue-600">{formattedProduction} FP/s</span>
          </div>
        </div>

        <button
          onClick={onPurchase}
          disabled={!canAfford}
          className={`py-1 px-3 rounded-md text-white text-sm font-medium transition-colors ${
            isFirstPurchase && canAfford
              ? 'bg-yellow-500 hover:bg-yellow-600 active:bg-yellow-700'
              : canAfford
                ? 'bg-green-500 hover:bg-green-600 active:bg-green-700'
                : 'bg-gray-400 cursor-not-allowed'
          }`}
        >
          Buy: {formattedCost} FP
        </button>
      </div>
    </div>
  );
};

export default BuildingCard;