import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { BuildingId } from '../../game/buildings/types';
import { RootState } from '../../state/store';
import { purchaseBuilding, selectBuilding, selectBuildingCost } from '../../state/slices/buildingsSlice';
import { spendFaithPoints } from '../../state/slices/faithPointsSlice';
import BuildingCard from './BuildingCard';

const FollowerBuilding: React.FC = () => {
  const dispatch = useDispatch();

  // Get the follower building and current faith points
  const follower = useSelector((state: RootState) => selectBuilding(state, BuildingId.FOLLOWER));
  const faithPoints = useSelector((state: RootState) => state.faithPoints.count);

  // Calculate if player can afford to buy
  const cost = useSelector((state: RootState) => selectBuildingCost(state, BuildingId.FOLLOWER));
  const canAfford = faithPoints >= cost;

  // Check if this would be the first follower
  const isFirstPurchase = follower.count === 0;

  const handlePurchase = () => {
    if (canAfford) {
      // Spend the faith points
      dispatch(spendFaithPoints(cost));
      // Purchase the building
      dispatch(purchaseBuilding(BuildingId.FOLLOWER));
    }
  };

  return (
    <BuildingCard
      building={follower}
      onPurchase={handlePurchase}
      canAfford={canAfford}
      isFirstPurchase={isFirstPurchase}
    />
  );
};

export default FollowerBuilding;