// Sound utility for managing game sounds

// Class for managing game sounds
class GameSounds {
  private static instance: GameSounds;
  private audioContext: AudioContext | null = null;
  private isMuted: boolean = false;
  private hasPlayedSound: boolean = false;

  private constructor() {
    // Initialize audio context when needed (on first play)
    // This helps with browser autoplay policies
    this.initAudioContext();
  }

  public static getInstance(): GameSounds {
    if (!GameSounds.instance) {
      GameSounds.instance = new GameSounds();
    }
    return GameSounds.instance;
  }

  private initAudioContext(): void {
    if (!this.audioContext) {
      try {
        // Check if Web Audio API is supported
        if (typeof AudioContext !== "undefined" || typeof (window as any).webkitAudioContext !== "undefined") {
          const AudioContextClass = window.AudioContext || (window as any).webkitAudioContext;
          this.audioContext = new AudioContextClass();
          console.log("Audio context initialized successfully");
        } else {
          console.warn("Web Audio API is not supported in this browser");
        }
      } catch (error) {
        console.error("Failed to create audio context:", error);
      }
    }
  }

  public playPrayerComplete(): void {
    if (this.isMuted) {
      console.log("Sound is muted, not playing");
      return;
    }

    try {
      // Make sure we have an audio context
      this.initAudioContext();

      if (!this.audioContext) {
        console.error("Cannot play sound: Audio context not available");
        return;
      }

      // Resume audio context if it's in suspended state (needed for Chrome)
      if (this.audioContext.state === "suspended") {
        console.log("Resuming suspended audio context");
        this.audioContext.resume().then(() => {
          console.log("Audio context resumed successfully");
          this.generateAndPlaySound();
        }).catch(err => {
          console.error("Error resuming audio context:", err);
        });
      } else {
        this.generateAndPlaySound();
      }

      // Mark that we've played a sound
      this.hasPlayedSound = true;
    } catch (error) {
      console.error('Error playing sound:', error);
    }
  }

  private generateAndPlaySound(): void {
    if (!this.audioContext) return;

    console.log("Generating prayer complete sound...");

    // Create oscillator for a simple bell-like sound
    const oscillator = this.audioContext.createOscillator();
    const gainNode = this.audioContext.createGain();

    // Connect nodes
    oscillator.connect(gainNode);
    gainNode.connect(this.audioContext.destination);

    // Set properties for bell-like sound
    oscillator.type = 'sine';
    oscillator.frequency.setValueAtTime(880, this.audioContext.currentTime); // A5 note

    // Envelope for bell-like sound
    gainNode.gain.setValueAtTime(0, this.audioContext.currentTime);
    gainNode.gain.linearRampToValueAtTime(0.5, this.audioContext.currentTime + 0.02); // Increased volume
    gainNode.gain.exponentialRampToValueAtTime(0.001, this.audioContext.currentTime + 1.5);

    // Start and stop
    oscillator.start(this.audioContext.currentTime);
    oscillator.stop(this.audioContext.currentTime + 1.5);

    console.log("Prayer complete sound played successfully");
  }

  public toggleMute(): boolean {
    this.isMuted = !this.isMuted;
    console.log(`Sound is now ${this.isMuted ? 'muted' : 'unmuted'}`);
    return this.isMuted;
  }

  public isSoundMuted(): boolean {
    return this.isMuted;
  }

  public hasAudio(): boolean {
    return this.audioContext !== null;
  }

  public hasEverPlayedSound(): boolean {
    return this.hasPlayedSound;
  }
}

export default GameSounds;