import React, { useCallback, useEffect, useState, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  startPraying,
  updatePrayProgress,
  completePrayer
} from '../../state/slices/faithPointsSlice';
import { RootState } from '../../state/store';
import GameSounds from '../../utils/audio/sounds';
import { selectTotalProduction } from '../../state/slices/buildingsSlice';

// Create a separate stylesheet for our animations
const animationStyles = `
  @keyframes fadeInOut {
    0% { opacity: 0; }
    10% { opacity: 1; }
    50% { opacity: 1; }
    100% { opacity: 0; }
  }

  .fade-in-out {
    animation: fadeInOut 2s forwards;
  }

  @keyframes scaleUp {
    0% { transform: scale(1); color: white; }
    10% { transform: scale(1.25); color: #facc15; }
    50% { transform: scale(1.25); color: #facc15; }
    100% { transform: scale(1); color: white; }
  }

  .scale-up {
    animation: scaleUp 2s forwards;
  }

  @keyframes pulse {
    0% { transform: scale(0.95); }
    50% { transform: scale(1.05); }
    100% { transform: scale(0.95); }
  }

  .light-pulse {
    animation: pulse 2s infinite;
  }

  @keyframes shimmer {
    0% { background-position: -200% 0; }
    100% { background-position: 200% 0; }
  }

  .progress-shimmer {
    background: linear-gradient(90deg,
      rgba(59, 130, 246, 0.8) 25%,
      rgba(96, 165, 250, 1) 50%,
      rgba(59, 130, 246, 0.8) 75%);
    background-size: 200% 100%;
    animation: shimmer 2s infinite linear;
  }

  @keyframes goldShimmer {
    0% { background-position: -200% 0; }
    100% { background-position: 200% 0; }
  }

  .gold-shimmer {
    background: linear-gradient(90deg,
      #facc15 0%,
      #fef08a 50%,
      #facc15 100%);
    background-size: 200% 100%;
    animation: goldShimmer 2s infinite linear;
  }

  @keyframes spinGlow {
    0% { transform: rotate(0deg); opacity: 0.6; }
    50% { opacity: 0.9; }
    100% { transform: rotate(360deg); opacity: 0.6; }
  }

  @keyframes glow {
    0% { box-shadow: 0 0 5px rgba(59, 130, 246, 0.5); }
    50% { box-shadow: 0 0 15px rgba(59, 130, 246, 0.8); }
    100% { box-shadow: 0 0 5px rgba(59, 130, 246, 0.5); }
  }

  @keyframes sparkle {
    0% { transform: scale(0); opacity: 0; }
    50% { transform: scale(1.2); opacity: 1; }
    100% { transform: scale(0); opacity: 0; }
  }

  .divine-sparkle {
    animation: sparkle 1.5s infinite ease-in-out;
  }

  @keyframes bounce {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
  }

  .bounce-animation {
    animation: bounce 2s infinite ease-in-out;
  }
`;

// Sound player interface (not exported to fix HMR issues)
interface SoundPlayer {
  playPrayerComplete: () => void;
}

// Default sound player using GameSounds (not exported to fix HMR issues)
const createDefaultSoundPlayer = (): SoundPlayer => {
  const soundsInstance = GameSounds.getInstance();
  return {
    playPrayerComplete: () => soundsInstance.playPrayerComplete()
  };
};

interface FaithPointProps {
  soundPlayer?: SoundPlayer;
}

const FaithPoint: React.FC<FaithPointProps> = ({
  soundPlayer = createDefaultSoundPlayer()
}) => {
  const dispatch = useDispatch();
  const { count, isPraying, prayProgress } = useSelector(
    (state: RootState) => state.faithPoints
  );

  // Get total FP production from buildings
  const totalProduction = useSelector(selectTotalProduction);

  // State for feedback animation
  const [showFeedback, setShowFeedback] = useState(false);
  const [animationKey, setAnimationKey] = useState(0);
  const [manualPrayerCompleted, setManualPrayerCompleted] = useState(false);
  const timerRef = useRef<NodeJS.Timeout | null>(null);

  // Initialize on first render - this prevents animation on page load
  useEffect(() => {
    // Mark as initialized after a short delay to ensure we don't trigger animation
    const initTimer = setTimeout(() => {
      console.log("FaithPoint component initialized");
    }, 100);

    return () => {
      clearTimeout(initTimer);
      if (timerRef.current) {
        clearTimeout(timerRef.current);
      }
    };
  }, [count]);

  // Listen for completePrayer action to detect manual prayer completion
  useEffect(() => {
    if (!isPraying && prayProgress === 0 && manualPrayerCompleted) {
      console.log("Manual prayer completed, showing effects");

      // Clear any existing timer
      if (timerRef.current) {
        clearTimeout(timerRef.current);
        timerRef.current = null;
      }

      // Show animation
      setAnimationKey(prev => prev + 1);
      setShowFeedback(true);

      // Play prayer complete sound
      try {
        soundPlayer.playPrayerComplete();
      } catch (error) {
        console.error("Failed to play prayer complete sound:", error);
      }

      // Set a timer to hide the feedback after animation completes
      timerRef.current = setTimeout(() => {
        setShowFeedback(false);
        timerRef.current = null;
      }, 2000);

      // Reset the manual prayer flag
      setManualPrayerCompleted(false);
    }
  }, [count, isPraying, prayProgress, soundPlayer, manualPrayerCompleted]);

  // Handle clicks on the faith point
  const handleClick = useCallback(() => {
    if (!isPraying) {
      dispatch(startPraying());

      // Start the prayer progress
      const startTime = Date.now();
      const totalPrayerTime = 2000; // 2 seconds

      const updateProgress = () => {
        const elapsed = Date.now() - startTime;
        const progress = Math.min(100, (elapsed / totalPrayerTime) * 100);

        dispatch(updatePrayProgress(progress));

        if (progress < 100) {
          requestAnimationFrame(updateProgress);
        } else {
          // Flag that a manual prayer is being completed
          setManualPrayerCompleted(true);
          dispatch(completePrayer());
          console.log("Prayer completed, dispatch completePrayer action");
        }
      };

      requestAnimationFrame(updateProgress);
    }
  }, [dispatch, isPraying]);

  return (
    <div className="relative flex flex-col items-center justify-center h-[60vh] w-full bg-black">
      {/* Add our custom animation styles */}
      <style>{animationStyles}</style>

      {/* Faith points counter with animation */}
      <div className="absolute top-4 right-4 text-white">
        {/* Use a wrapper with key to force animation refresh */}
        <div className="flex flex-col items-end">
          <span key={`fp-counter-${animationKey}`} className={`text-xl font-bold ${showFeedback ? 'scale-up' : ''}`}>
            {Math.floor(count)} FP
          </span>
          {totalProduction > 0 && (
            <span className="text-sm text-green-400">+{Math.floor(totalProduction)} FP/s</span>
          )}
        </div>
      </div>

      {/* Container for icon and progress bar with fixed layout */}
      <div className="flex flex-col items-center">
        {/* Faith point light source */}
        <div
          className={`relative w-16 h-16 rounded-full cursor-pointer mb-8
                    ${isPraying ? 'opacity-50' : 'opacity-100 light-pulse'}
                    ${count === 0 ? 'bounce-animation' : ''}
                    transition-all duration-300 ease-in-out`}
          onClick={handleClick}
        >
          {/* Multiple light layers for glow effect */}
          <div className="absolute inset-0 bg-blue-200 rounded-full opacity-20 blur-md"></div>
          <div className="absolute inset-2 bg-blue-300 rounded-full opacity-30 blur-sm"></div>
          <div className="absolute inset-4 bg-white rounded-full shadow-lg shadow-blue-500/50"></div>

          {/* Label for new players */}
          {count === 0 && !isPraying && (
            <div className="absolute -bottom-8 w-32 text-center left-1/2 -translate-x-1/2 text-sm text-blue-300 font-semibold whitespace-nowrap">
              Click to pray
            </div>
          )}
        </div>

        {/* Prayer progress indicator */}
        <div className="h-12">
          {isPraying && (
            <div className="flex flex-col items-center">
              {/* Progress bar with text inside */}
              <div className="relative w-64 h-10 bg-gray-800 rounded-lg overflow-hidden shadow-inner flex items-center justify-center">
                {/* Animated progress indicator (positioned behind the text) */}
                <div
                  className="absolute left-0 top-0 h-full rounded-lg progress-shimmer"
                  style={{ width: `${prayProgress}%` }}
                ></div>

                {/* Sparkle effects */}
                {prayProgress > 20 && (
                  <>
                    <div className="absolute h-1 w-1 bg-blue-200 rounded-full divine-sparkle"
                         style={{ left: `${prayProgress/3}%`, top: '20%', animationDelay: '0.2s' }}></div>
                    <div className="absolute h-1 w-1 bg-blue-200 rounded-full divine-sparkle"
                         style={{ left: `${prayProgress/2}%`, top: '70%', animationDelay: '0.5s' }}></div>
                  </>
                )}

                {prayProgress > 50 && (
                  <>
                    <div className="absolute h-1 w-1 bg-blue-100 rounded-full divine-sparkle"
                         style={{ left: `${prayProgress/1.5}%`, top: '40%', animationDelay: '0.3s' }}></div>
                    <div className="absolute h-0.5 w-0.5 bg-white rounded-full divine-sparkle"
                         style={{ left: `${prayProgress/1.2}%`, top: '60%', animationDelay: '0.7s' }}></div>
                  </>
                )}

                {/* Prayer text */}
                <div className="text-gray-200 text-sm font-medium relative z-10 px-2 text-center whitespace-nowrap">
                  Answering simple prayer... {Math.round(prayProgress)}%
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default FaithPoint;