import { useEffect, useRef, useCallback } from 'react';
import { useDispatch } from 'react-redux';
// We'll implement these later
// import { updateGameState } from '../state/slices/gameSlice';
// import { selectGameSpeed } from '../state/selectors/settingsSelectors';

type TickHandler = (deltaTime: number) => void;

export function useGameLoop() {
  const dispatch = useDispatch();
  // Placeholder until we implement the actual selector
  const gameSpeed = 1; // useSelector(selectGameSpeed);
  const lastUpdateRef = useRef<number>(0);
  const accumulatorRef = useRef<number>(0);
  const tickHandlersRef = useRef<Map<string, TickHandler>>(new Map());
  const loopActiveRef = useRef<boolean>(true);

  // Fixed timestep for game logic (milliseconds)
  const FIXED_TIMESTEP = 50; // 20 updates per second

  const tick = useCallback((timestamp: number) => {
    // Skip if not active
    if (!loopActiveRef.current) {
      console.log("Game loop is not active, skipping tick");
      return;
    }

    if (lastUpdateRef.current === 0) {
      lastUpdateRef.current = timestamp;
      requestAnimationFrame(tick);
      return;
    }

    const deltaTime = (timestamp - lastUpdateRef.current) * gameSpeed;
    lastUpdateRef.current = timestamp;

    // Add to accumulator
    accumulatorRef.current += deltaTime;

    // Process fixed logic updates
    let updatesProcessed = 0;
    while (accumulatorRef.current >= FIXED_TIMESTEP && updatesProcessed < 10) {
      // Update game state
      // dispatch(updateGameState(FIXED_TIMESTEP / 1000)); // Convert to seconds

      // Log handler count
      if (tickHandlersRef.current.size > 0) {
        console.log(`Running ${tickHandlersRef.current.size} tick handlers with dt=${FIXED_TIMESTEP / 1000}s`);
      }

      // Run registered tick handlers
      tickHandlersRef.current.forEach((handler) => handler(FIXED_TIMESTEP / 1000));

      accumulatorRef.current -= FIXED_TIMESTEP;
      updatesProcessed++;
    }

    // Safety check - if we're getting a build-up, discard excess to avoid spiral of death
    if (updatesProcessed >= 10) {
      console.warn("Too many updates, discarding accumulation to avoid freezing");
      accumulatorRef.current = 0;
    }

    // Continue the loop
    requestAnimationFrame(tick);
  }, [dispatch, gameSpeed]);

  // Start/stop the game loop
  useEffect(() => {
    console.log("Starting game loop");
    loopActiveRef.current = true;
    const animationId = requestAnimationFrame(tick);

    return () => {
      console.log("Stopping game loop");
      loopActiveRef.current = false;
      cancelAnimationFrame(animationId);
    };
  }, [tick]);

  // Register/unregister tick handlers
  const registerTickHandler = useCallback((handler: TickHandler): string => {
    const id = Math.random().toString(36).substring(2, 9);
    tickHandlersRef.current.set(id, handler);
    console.log(`Registered tick handler ${id}, total handlers: ${tickHandlersRef.current.size}`);
    return id;
  }, []);

  const unregisterTickHandler = useCallback((id: string) => {
    const wasRemoved = tickHandlersRef.current.delete(id);
    console.log(`Unregistered tick handler ${id} (success: ${wasRemoved}), total handlers: ${tickHandlersRef.current.size}`);
  }, []);

  return { registerTickHandler, unregisterTickHandler };
}