import { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import AppShell from './components/AppShell';
import { useGameLoop } from './hooks/useGameLoop';
import { loadGameState } from './utils/storage/gameStorage';
import { loadSavedFaithPoints, addFaithPointsFromBuildings } from './state/slices/faithPointsSlice';
import { loadSavedBuildings, selectTotalProduction } from './state/slices/buildingsSlice';

function App() {
  const dispatch = useDispatch();
  const { registerTickHandler, unregisterTickHandler } = useGameLoop();

  // Track initialization state to prevent multiple loads
  const [isInitialized, setIsInitialized] = useState(false);

  // Get total building production
  const totalProduction = useSelector(selectTotalProduction);

  // Log total production for debugging
  console.log('Current total production:', totalProduction);

  // Store tick handler ID in a ref so we can update it when production changes
  const tickHandlerIdRef = useRef<string | null>(null);
  // Track accumulated fractional faith points
  const accumulatedPointsRef = useRef<number>(0);

  // Load saved game state
  useEffect(() => {
    // Only initialize once
    if (isInitialized) return;

    console.log("Initializing game state");

    // Check if we just reset the game via two possible methods:
    // 1. Session storage flag
    const justReset = sessionStorage.getItem('just_reset') === 'true';

    // 2. URL parameter with reset timestamp (more reliable)
    const urlParams = new URLSearchParams(window.location.search);
    const resetParam = urlParams.get('reset');

    // Clear reset parameters from URL to prevent persistent reset state
    if (resetParam) {
      const cleanUrl = window.location.pathname + window.location.hash;
      window.history.replaceState({}, document.title, cleanUrl);
    }

    if (justReset || resetParam) {
      console.log("Reset detected - starting with clean state");
      // Clear the reset flag
      sessionStorage.removeItem('just_reset');
      // Skip loading saved state
      setIsInitialized(true);
      return;
    }

    // Load game from storage if exists
    const savedState = loadGameState();
    if (savedState) {
      // Initialize faith points if saved
      if (savedState.faithPoints) {
        console.log("Loading saved faith points:", Math.floor(savedState.faithPoints.count));
        dispatch(loadSavedFaithPoints(savedState.faithPoints.count));
      }

      // Initialize buildings if saved
      if (savedState.buildings) {
        console.log("Loading saved buildings:", savedState.buildings);
        dispatch(loadSavedBuildings(savedState.buildings));
      }
    } else {
      console.log("No saved state found, using default state");
    }

    setIsInitialized(true);
  }, [dispatch, isInitialized]);

  // Setup game tick handler that will refresh when totalProduction changes
  useEffect(() => {
    // Skip if not initialized
    if (!isInitialized) return;

    // Unregister previous handler if it exists
    if (tickHandlerIdRef.current) {
      unregisterTickHandler(tickHandlerIdRef.current);
      tickHandlerIdRef.current = null;
    }

    // Only set up production if we have any
    if (totalProduction > 0) {
      console.log('Setting up game tick handler with production rate:', totalProduction);

      // Reset accumulated points when production changes
      accumulatedPointsRef.current = 0;

      // Register new tick handler with the current production value
      const tickHandlerId = registerTickHandler((deltaTime: number) => {
        // Calculate faith points generated this tick
        if (totalProduction > 0) {
          // Account for very small deltaTime values by accumulating
          const pointsGenerated = totalProduction * deltaTime;

          // Add to accumulated total
          accumulatedPointsRef.current += pointsGenerated;

          // When we accumulate at least 1 whole point, dispatch it
          if (accumulatedPointsRef.current >= 1) {
            const pointsToAdd = Math.floor(accumulatedPointsRef.current);
            dispatch(addFaithPointsFromBuildings(pointsToAdd));

            // Keep only the fractional part
            accumulatedPointsRef.current -= pointsToAdd;
          }
        }
      });

      // Store the handler ID for cleanup
      tickHandlerIdRef.current = tickHandlerId;
    }

    // Cleanup handler on unmount or when production changes
    return () => {
      if (tickHandlerIdRef.current) {
        unregisterTickHandler(tickHandlerIdRef.current);
        tickHandlerIdRef.current = null;
      }
    };
  }, [dispatch, registerTickHandler, unregisterTickHandler, totalProduction, isInitialized]);

  return (
    <div className="app">
      <AppShell />
    </div>
  );
}

export default App;