/**
 * Types for buildings in the game
 */

export interface Building {
  id: string;
  name: string;
  description: string;
  icon: string;
  count: number;
  baseCost: number;
  costMultiplier: number;
  baseProduction: number;
}

export interface BuildingState {
  followers: Building;
  // Future buildings will be added here
}

// Building IDs as constants to avoid typos
export enum BuildingId {
  FOLLOWER = 'followers',
}