import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface FaithPointsState {
  count: number;
  isPraying: boolean;
  prayProgress: number; // 0 to 100
}

const initialState: FaithPointsState = {
  count: 0,
  isPraying: false,
  prayProgress: 0,
};

export const faithPointsSlice = createSlice({
  name: 'faithPoints',
  initialState,
  reducers: {
    loadSavedFaithPoints: (state, action: PayloadAction<number>) => {
      // Directly set the count without incrementing
      state.count = action.payload;
    },
    resetFaithPoints: (state) => {
      // Reset to initial state
      state.count = initialState.count;
      state.isPraying = initialState.isPraying;
      state.prayProgress = initialState.prayProgress;
    },
    startPraying: (state) => {
      state.isPraying = true;
      state.prayProgress = 0;
    },
    updatePrayProgress: (state, action: PayloadAction<number>) => {
      state.prayProgress = action.payload;
    },
    completePrayer: (state) => {
      state.count += 1;
      state.isPraying = false;
      state.prayProgress = 0;
    },
    cancelPrayer: (state) => {
      state.isPraying = false;
      state.prayProgress = 0;
    },
    // New action to add faith points from buildings
    addFaithPointsFromBuildings: (state, action: PayloadAction<number>) => {
      state.count += action.payload;
    },
    // Action to spend faith points (for buying buildings)
    spendFaithPoints: (state, action: PayloadAction<number>) => {
      state.count -= action.payload;
    },
  },
});

export const {
  loadSavedFaithPoints,
  resetFaithPoints,
  startPraying,
  updatePrayProgress,
  completePrayer,
  cancelPrayer,
  addFaithPointsFromBuildings,
  spendFaithPoints
} = faithPointsSlice.actions;

export default faithPointsSlice.reducer;