import { Middleware } from '@reduxjs/toolkit';
import { saveFaithPoints, saveBuildings } from './gameStorage';
import { completePrayer, spendFaithPoints, addFaithPointsFromBuildings } from '../../state/slices/faithPointsSlice';
import { purchaseBuilding } from '../../state/slices/buildingsSlice';

/**
 * Creates an auto-save middleware that responds to certain actions by saving game state.
 *
 * This middleware will automatically save the game state when specific actions occur.
 *
 * @param saveFaithPointsFunc - The function used to save faith points (allows for dependency injection in tests)
 * @param saveBuildingsFunc - The function used to save buildings state
 * @returns A Redux middleware that handles auto-saving
 */
export const createAutoSaveMiddleware = (
  saveFaithPointsFunc: typeof saveFaithPoints = saveFaithPoints,
  saveBuildingsFunc: typeof saveBuildings = saveBuildings
): Middleware => {
  return store => next => action => {
    // Process the action first
    const result = next(action);

    // Auto-save after specific actions
    if (
      action.type === completePrayer.type ||
      action.type === spendFaithPoints.type ||
      action.type === addFaithPointsFromBuildings.type
    ) {
      saveFaithPointsFunc(store.getState());
    }

    // Save buildings state when purchased
    if (action.type === purchaseBuilding.type) {
      saveBuildingsFunc(store.getState());
      // Also save faith points since they were spent
      saveFaithPointsFunc(store.getState());
    }

    return result;
  };
};

// Default middleware instance
export const autoSaveMiddleware = createAutoSaveMiddleware();