import { RootState } from '../../state/store';

// Define constants for all storage keys
export const SAVE_KEY = 'incremental_game_save';
// Extended list of keys to clear, including any possible old keys or variations
const KEYS_TO_CLEAR = [
  SAVE_KEY,
  'incremental_game_save',
  'incremental_game_state',
  'incremental_game_settings',
  'incremental_game_cache',
  'faith_points',
  'buildings',
  'incremental_game_timestamp',
  'deity_game_save',
  'incremental_cache',
  'game_progress',
  'game_state',
  'game_data',
  'save_data',
  'user_progress',
  'faith_point_counter',
  'incremental_progress'
];

// Storage adapter interface
export interface StorageAdapter {
  getItem: (key: string) => string | null;
  setItem: (key: string, value: string) => void;
  removeItem: (key: string) => void;
}

// Default adapter using browser localStorage
const defaultStorageAdapter: StorageAdapter = {
  getItem: (key: string) => localStorage.getItem(key),
  setItem: (key: string, value: string) => localStorage.setItem(key, value),
  removeItem: (key: string) => localStorage.removeItem(key)
};

// Save the game state to storage
export const saveGameState = (
  state: Partial<RootState>,
  storageAdapter: StorageAdapter = defaultStorageAdapter
): void => {
  try {
    const serializedState = JSON.stringify(state);
    storageAdapter.setItem(SAVE_KEY, serializedState);
  } catch (err) {
    console.error('Could not save game state:', err);
  }
};

// Load the game state from storage
export const loadGameState = (
  storageAdapter: StorageAdapter = defaultStorageAdapter
): Partial<RootState> | undefined => {
  try {
    // Check if there's a reset flag in the URL
    // If so, return undefined so we start fresh
    const urlParams = new URLSearchParams(window.location.search);
    if (urlParams.has('reset')) {
      console.log('Reset parameter detected, not loading saved state');
      return undefined;
    }

    // Check session storage reset flag
    if (sessionStorage.getItem('just_reset') === 'true') {
      console.log('Reset flag detected in session storage, not loading saved state');
      return undefined;
    }

    const serializedState = storageAdapter.getItem(SAVE_KEY);
    if (!serializedState) return undefined;

    // Parse with safety check
    try {
      const parsedState = JSON.parse(serializedState);
      return parsedState;
    } catch (parseErr) {
      console.error('Error parsing saved state, starting fresh:', parseErr);
      return undefined;
    }
  } catch (err) {
    console.error('Could not load game state:', err);
    return undefined;
  }
};

// Clear all saved game data
export const clearGameState = (
  storageAdapter: StorageAdapter = defaultStorageAdapter
): void => {
  try {
    console.log('Clearing all game state data from storage');

    // First remove the main save key
    storageAdapter.removeItem(SAVE_KEY);

    // In normal usage, clear all potential keys
    if (storageAdapter === defaultStorageAdapter) {
      // Try to clear all localStorage
      try {
        // Check if we're in a real browser environment
        const isRealBrowser = typeof localStorage !== 'undefined' &&
                             typeof localStorage.key === 'function' &&
                             typeof window !== 'undefined';

        // Log all current keys before clearing, for debugging
        if (isRealBrowser) {
          const keys = [];
          for (let i = 0; i < localStorage.length; i++) {
            const key = localStorage.key(i);
            if (key) keys.push(key);
          }
          console.log('Current localStorage keys before clearing:', keys);
        }

        // Clear all localStorage
        localStorage.clear();

        // Double check after clearing
        if (isRealBrowser) {
          console.log('localStorage items after clearing:', localStorage.length);
        }
      } catch (err) {
        console.warn('Could not clear all localStorage:', err);

        // Fallback: remove individual keys
        for (const key of KEYS_TO_CLEAR) {
          try {
            storageAdapter.removeItem(key);
            console.log(`Removed key: ${key}`);
          } catch (innerErr) {
            // Ignore errors for keys that don't exist
          }
        }
      }

      // Also try to clear sessionStorage for our reset flag
      try {
        sessionStorage.removeItem('just_reset');
      } catch (err) {
        // Ignore this error
      }
    } else {
      // In test environment, just remove the main key
      // This is handled by the first removal above
    }

    console.log('Game state cleared successfully');
  } catch (err) {
    console.error('Could not clear game state:', err);
  }
};

// Save only the faith points part of the state
export const saveFaithPoints = (
  state: RootState,
  loadStateFunc: typeof loadGameState = loadGameState,
  saveStateFunc: typeof saveGameState = saveGameState
): void => {
  try {
    // Don't save if reset is in progress
    if (sessionStorage.getItem('just_reset') === 'true') {
      console.log('Reset in progress, not saving faith points');
      return;
    }

    const { faithPoints } = state;
    const currentSave = loadStateFunc() || {};

    saveStateFunc({
      ...currentSave,
      faithPoints,
    });
  } catch (err) {
    console.error('Could not save faith points:', err);
  }
};

