import React, { useState, useEffect, useCallback } from 'react';
import GameSounds from '../../utils/audio/sounds';

interface SoundControlProps {
  inSidebar?: boolean;
}

const SoundControl: React.FC<SoundControlProps> = ({ inSidebar = false }) => {
  const [isMuted, setIsMuted] = useState(false);
  const [audioStatus, setAudioStatus] = useState('initializing');
  const soundsInstance = GameSounds.getInstance();

  // Initialize mute status from GameSounds
  useEffect(() => {
    setIsMuted(soundsInstance.isSoundMuted());

    // Check audio status
    if (soundsInstance.hasAudio()) {
      setAudioStatus('ready');
    } else {
      setAudioStatus('unavailable');
    }

    // Re-check status periodically
    const interval = setInterval(() => {
      if (soundsInstance.hasEverPlayedSound()) {
        setAudioStatus('active');
        clearInterval(interval);
      }
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  const toggleMute = useCallback(() => {
    const newMuteState = soundsInstance.toggleMute();
    setIsMuted(newMuteState);
  }, [soundsInstance]);

  const testSound = useCallback(() => {
    console.log("Testing sound from sound control");
    soundsInstance.playPrayerComplete();
  }, [soundsInstance]);

  if (inSidebar) {
    // Render controls suitable for the sidebar
    return (
      <div className="flex flex-col space-y-2">
        <button
          onClick={toggleMute}
          className={`px-4 py-2 text-white text-sm font-medium rounded shadow-md w-full ${
            isMuted ? 'bg-gray-600 hover:bg-gray-500' : 'bg-gray-700 hover:bg-gray-600'
          }`}
          title={isMuted ? "Unmute game sounds" : "Mute game sounds"}
        >
          {isMuted ? ' Unmute Sounds' : ' Mute Sounds'}
        </button>

        <button
          onClick={testSound}
          className="px-4 py-2 bg-blue-700 text-white text-sm font-medium rounded hover:bg-blue-600 shadow-md w-full"
          title="Test prayer completion sound"
        >
           Test Sound
        </button>

        <div className="mt-2 text-sm bg-gray-700 p-2 rounded">
          Sound: {audioStatus === 'ready' ? ' Ready' : audioStatus === 'active' ? ' Active' : ' Not available'}
        </div>
      </div>
    );
  }

  // Standalone version (fixed position)
  return (
    <div className="absolute top-2 right-2 z-10 flex flex-col items-end gap-2">
      <button
        onClick={toggleMute}
        className="px-3 py-1 bg-gray-700 text-white text-xs font-mono rounded hover:bg-gray-600 border border-gray-800 shadow-md"
        title={isMuted ? "Unmute game sounds" : "Mute game sounds"}
      >
        {isMuted ? ' UNMUTE' : ' MUTE'}
      </button>

      <button
        onClick={testSound}
        className="px-3 py-1 bg-blue-700 text-white text-xs font-mono rounded hover:bg-blue-600 border border-blue-800 shadow-md"
        title="Test prayer completion sound"
      >
         TEST SOUND
      </button>

      <div className="text-xs text-white bg-black bg-opacity-50 px-2 py-1 rounded">
        Sound: {audioStatus === 'ready' ? ' Ready' : audioStatus === 'active' ? ' Active' : ' Not available'}
      </div>
    </div>
  );
};

export default SoundControl;