import React, { useState, useCallback, useEffect } from 'react';
import DebugTools from './DebugTools';
import SoundControl from './SoundControl';
import env from '../../config/env';

const DebugSidebar: React.FC = () => {
  // Start with the sidebar open by default in development, closed in production
  const [isOpen, setIsOpen] = useState(env.isDevelopment);

  const toggleSidebar = useCallback(() => {
    setIsOpen(prev => !prev);
  }, []);

  // Apply margin to the main content when sidebar is open
  useEffect(() => {
    const mainContent = document.querySelector('.app-shell');
    if (mainContent) {
      if (isOpen) {
        // Add padding to the left of the main content
        mainContent.classList.add('pl-64'); // 16rem = 64 sidebar width
        // Add transition effect
        mainContent.classList.add('transition-padding');
        mainContent.classList.add('duration-300');
      } else {
        // Remove padding when sidebar is closed
        mainContent.classList.remove('pl-64');
      }
    }

    return () => {
      // Cleanup on unmount
      if (mainContent) {
        mainContent.classList.remove('pl-64');
        mainContent.classList.remove('transition-padding');
        mainContent.classList.remove('duration-300');
      }
    };
  }, [isOpen]);

  return (
    <>
      {/* Hamburger menu button - keep this fixed */}
      <button
        onClick={toggleSidebar}
        className={`fixed top-4 left-4 z-50 p-2 rounded-md transition-colors duration-200 ${
          isOpen ? 'bg-gray-700 text-white' : 'bg-black bg-opacity-50 text-white hover:bg-gray-700'
        }`}
        aria-label="Toggle debug menu"
      >
        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
        </svg>
      </button>

      {/* Debug sidebar - position fixed but push content */}
      <div className={`fixed top-0 left-0 z-40 h-full w-64 bg-gray-800 text-white shadow-lg transform transition-transform duration-300 ease-in-out ${
        isOpen ? 'translate-x-0' : '-translate-x-full'
      }`}>
        <div className="p-4 pt-16"> {/* Added top padding for the hamburger menu */}
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-xl font-bold">Debug Tools</h2>
            <button onClick={toggleSidebar} className="text-white hover:text-gray-300">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>

          <div className="space-y-6">
            {/* Debug tools section */}
            <div>
              <h3 className="text-lg font-semibold mb-2">Game Controls</h3>
              <div className="w-full">
                <DebugTools inSidebar={true} />
              </div>
            </div>

            {/* Sound controls section */}
            <div>
              <h3 className="text-lg font-semibold mb-2">Sound Controls</h3>
              <div className="w-full">
                <SoundControl inSidebar={true} />
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* No need for backdrop when we're pushing content */}
    </>
  );
};

export default DebugSidebar;