import { configureStore, Middleware, Action, ThunkAction } from '@reduxjs/toolkit';
import { combineReducers } from 'redux';
// Importing our faith points reducer
import faithPointsReducer from './slices/faithPointsSlice';
// Import the buildings reducer
import buildingsReducer from './slices/buildingsSlice';
// Import the auto-save middleware
import { autoSaveMiddleware } from '../utils/storage/middleware';
// We'll add these reducers as they're implemented
// import resourcesReducer from './slices/resourcesSlice';
// import upgradesReducer from './slices/upgradesSlice';
// import timeReducer from './slices/timeSlice';
// import settingsReducer from './slices/settingsSlice';
// import uiReducer from './slices/uiSlice';

// Placeholder for actual reducers until implemented
const placeholderReducer = (state = {}) => state;

const rootReducer = combineReducers({
  // Add our faith points reducer
  faithPoints: faithPointsReducer,
  // Add our buildings reducer
  buildings: buildingsReducer,
  // Using placeholders for remaining reducers
  resources: placeholderReducer,
  upgrades: placeholderReducer,
  time: placeholderReducer,
  settings: placeholderReducer,
  ui: placeholderReducer,
});

// Pre-define AppStore to resolve circular reference
export type AppStore = ReturnType<typeof createAppStore>;
export type AppDispatch = AppStore['dispatch'];
export type RootState = ReturnType<typeof rootReducer>;
export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  RootState,
  unknown,
  Action<string>
>;

// Create store factory function
const createAppStore = () => {
  return configureStore({
    reducer: rootReducer,
    middleware: (getDefaultMiddleware) => {
      return getDefaultMiddleware().concat(autoSaveMiddleware as Middleware);
    },
  });
};

// Create the store instance
export const store = createAppStore();

// Export helper hooks
export default store;