import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { resetFaithPoints, addFaithPointsFromBuildings } from '../../state/slices/faithPointsSlice';
import { resetBuildings, selectTotalProduction } from '../../state/slices/buildingsSlice';
import { clearGameState } from '../../utils/storage/gameStorage';

interface DebugToolsProps {
  inSidebar?: boolean;
}

const DebugTools: React.FC<DebugToolsProps> = ({ inSidebar = false }) => {
  const dispatch = useDispatch();
  const totalProduction = useSelector(selectTotalProduction);

  const handleReset = () => {
    console.log("Resetting game state...");

    try {
      // 1. Reset Redux state FIRST (before any async operations)
      // These are synchronous and should update the state immediately
      dispatch(resetFaithPoints());
      dispatch(resetBuildings());
      console.log("Redux state reset - FP and buildings should be at 0");

      // 2. Manually clear ALL localStorage (except essential flags)
      try {
        // Log keys only if we're in a browser environment and not in tests
        const isRealBrowser = typeof localStorage !== 'undefined' &&
                             typeof localStorage.key === 'function' &&
                             typeof window !== 'undefined';

        if (isRealBrowser) {
          const allKeys = [...Array(localStorage.length)].map((_, i) => localStorage.key(i)).filter(Boolean) as string[];
          console.log("Clearing localStorage keys:", allKeys);
        }

        // Explicitly remove guidance state before clearing everything
        localStorage.removeItem('guidance_shown');

        // Clear all localStorage items
        localStorage.clear();
      } catch (localErr) {
        console.error("Error clearing localStorage:", localErr);
      }

      // 3. Clear session storage but keep or set reset flags
      try {
        // Clear all sessionStorage
        sessionStorage.clear();

        // Set the reset flag in sessionStorage to prevent reloading saved data
        sessionStorage.setItem('just_reset', 'true');
        console.log("Set reset flag in sessionStorage:", sessionStorage.getItem('just_reset'));
      } catch (sessionErr) {
        console.error("Error handling sessionStorage during reset:", sessionErr);
      }

      // 4. Use our clearGameState function which targets specific keys
      clearGameState();

      // Show confirmation without reload - the Redux state should already be reset
      console.log("Game state fully reset.");
      alert('Game reset complete! Your faith points and buildings should now be at 0.');
    } catch (err) {
      console.error("Error during reset:", err);
      alert('There was an error resetting the game. Please try again.');
    }
  };

  const handleAdd10FP = () => {
    // Add 10 faith points
    dispatch(addFaithPointsFromBuildings(10));
  };

  if (inSidebar) {
    // Render buttons suitable for the sidebar
    return (
      <div className="flex flex-col space-y-2">
        <div className="bg-gray-700 p-2 rounded mb-1">
          <div className="text-xs text-gray-300">Debug Info:</div>
          <div className="text-sm text-white">FP Production: {Math.floor(totalProduction)} FP/s</div>
        </div>

        <button
          onClick={handleAdd10FP}
          className="px-4 py-2 bg-green-600 text-white text-sm font-medium rounded hover:bg-green-700 border border-green-800 shadow-md w-full"
          title="Add 10 Faith Points"
        >
          +10 FP
        </button>

        <button
          onClick={handleReset}
          className="px-4 py-2 bg-red-600 text-white text-sm font-medium rounded hover:bg-red-700 border border-red-800 shadow-md w-full"
          title="Completely resets the game and clears saved data"
        >
          Reset Game
        </button>
      </div>
    );
  }

  // Standalone version (fixed position)
  return (
    <div className="absolute top-2 left-2 z-10 flex flex-col space-y-1">
      <div className="bg-gray-800 bg-opacity-70 p-1 rounded text-xs text-white">
        FP Production: {Math.floor(totalProduction)} FP/s
      </div>

      <button
        onClick={handleAdd10FP}
        className="px-3 py-1 bg-green-600 text-white text-xs font-mono rounded hover:bg-green-700 border border-green-800 shadow-md"
        title="Add 10 Faith Points"
      >
        DEBUG: +10 FP
      </button>

      <button
        onClick={handleReset}
        className="px-3 py-1 bg-red-600 text-white text-xs font-mono rounded hover:bg-red-700 border border-red-800 shadow-md"
        title="Completely resets the game and clears saved data"
      >
        DEBUG: RESET GAME
      </button>
    </div>
  );
};

export default DebugTools;