// Environment configuration for the application

// Define types for our app's environment configuration
interface EnvironmentConfig {
  apiUrl: string;
  isProduction: boolean;
  isDevelopment: boolean;
  version: string;
  basePath: string;
}

// When using Vite, these variables are automatically available:
// - import.meta.env.MODE: {string} the mode the app is running in
// - import.meta.env.PROD: {boolean} whether the app is running in production
// - import.meta.env.DEV: {boolean} whether the app is running in development
// - import.meta.env.BASE_URL: {string} the base URL the app is being served from

// Create the environment config
export const env: EnvironmentConfig = {
  apiUrl: import.meta.env.PROD
    ? 'https://api.yourgame.com' // Replace with actual API URL if you have one
    : 'http://localhost:3000',
  isProduction: import.meta.env.PROD,
  isDevelopment: import.meta.env.DEV,
  version: '0.1.0', // Hardcoded for now
  basePath: import.meta.env.BASE_URL
};

export default env;