/**
 * Calculations related to buildings
 */

import { Building } from './types';

/**
 * Calculate the current cost of a building based on its base cost, count, and multiplier
 * Uses the formula: baseCost * (multiplier^count)
 */
export const calculateBuildingCost = (building: Building): number => {
  return building.baseCost * Math.pow(building.costMultiplier, building.count);
};

/**
 * Calculate the total production of a building based on its count and base production
 */
export const calculateBuildingProduction = (building: Building): number => {
  return building.baseProduction * building.count;
};

/**
 * Check if a building can be purchased with the current resources
 */
export const canAffordBuilding = (building: Building, currentResources: number): boolean => {
  const cost = calculateBuildingCost(building);
  return currentResources >= cost;
};

/**
 * Format a number for display, rounding to 2 decimal places
 */
export const formatNumber = (num: number): string => {
  if (num === Math.floor(num)) {
    return num.toString();
  }
  return num.toFixed(2);
};