import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Building, BuildingId, BuildingState } from '../../game/buildings/types';
import { RootState } from '../store';
import { calculateBuildingCost, calculateBuildingProduction } from '../../game/buildings/calculations';

// Initial state for buildings
const initialState: BuildingState = {
  followers: {
    id: BuildingId.FOLLOWER,
    name: 'Follower',
    description: 'A simple follower. Says a simple prayer every day.',
    icon: '', // Simple prayer emoji as icon
    count: 0,
    baseCost: 10, // Costs 10 FP initially
    costMultiplier: 1.1, // 10% increase per purchase
    baseProduction: 1, // 1 FP per second
  },
};

export const buildingsSlice = createSlice({
  name: 'buildings',
  initialState,
  reducers: {
    purchaseBuilding: (state, action: PayloadAction<BuildingId>) => {
      const buildingId = action.payload;
      const building = state[buildingId];

      // Increment the count (the actual cost check is done before dispatching this action)
      if (building) {
        building.count += 1;
      }
    },

    loadSavedBuildings: (_, action: PayloadAction<BuildingState>) => {
      // Replace entire state with saved state
      return action.payload;
    },

    resetBuildings: () => {
      // Reset to initial state
      return initialState;
    },
  },
});

// Export actions
export const {
  purchaseBuilding,
  loadSavedBuildings,
  resetBuildings
} = buildingsSlice.actions;

// Selectors
export const selectAllBuildings = (state: RootState): BuildingState => state.buildings;

export const selectBuilding = (state: RootState, buildingId: BuildingId): Building =>
  state.buildings[buildingId];

export const selectBuildingCost = (state: RootState, buildingId: BuildingId): number => {
  const building = selectBuilding(state, buildingId);
  return calculateBuildingCost(building);
};

export const selectBuildingProduction = (state: RootState, buildingId: BuildingId): number => {
  const building = selectBuilding(state, buildingId);
  return calculateBuildingProduction(building);
};

// Calculate total production from all buildings
export const selectTotalProduction = (state: RootState): number => {
  const buildings = selectAllBuildings(state);

  // Check if buildings is null or undefined before using Object.values
  if (!buildings) {
    return 0;
  }

  return Object.values(buildings).reduce(
    (total, building) => total + calculateBuildingProduction(building),
    0
  );
};

export default buildingsSlice.reducer;