import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { RootState } from '../../state/store';

const PlayerGuidance: React.FC = () => {
  const [dismissed, setDismissed] = useState(false);
  const faithPoints = useSelector((state: RootState) => state.faithPoints.count);
  const isPraying = useSelector((state: RootState) => state.faithPoints.isPraying);
  const followerCount = useSelector((state: RootState) =>
    state.buildings.followers.count || 0
  );

  // Determine which guidance to show based on game state
  const showPrayerGuidance = faithPoints < 1 && !isPraying && !dismissed;
  const showFollowerGuidance = faithPoints >= 10 && followerCount === 0 && !dismissed;

  // Add effect to watch for reset flag changes
  useEffect(() => {
    const handleReset = () => {
      console.log('Guidance reset detected via storage event');
      setDismissed(false);
    };

    // Listen for storage events that might indicate a reset
    window.addEventListener('storage', (e) => {
      if (e.key === 'just_reset' && e.newValue === 'true') {
        handleReset();
      }
    });

    return () => {
      window.removeEventListener('storage', handleReset);
    };
  }, []);

  // Check for reset signals on component mount and faith point changes
  useEffect(() => {
    // Check for reset signals
    const urlParams = new URLSearchParams(window.location.search);
    const resetParam = urlParams.get('reset');
    const justReset = sessionStorage.getItem('just_reset') === 'true';

    console.log('Checking reset status with FP:', faithPoints, { resetParam, justReset, dismissed });

    // If faith points is 0 and we have a reset flag, show guidance
    if (faithPoints === 0 && (resetParam || justReset || followerCount === 0)) {
      console.log('Reset or new game state detected in PlayerGuidance, showing guidance');
      // Clear the guidance_shown flag from localStorage if it exists
      localStorage.removeItem('guidance_shown');
      setDismissed(false);
      return;
    }

    // Otherwise, check if we've shown guidance before
    const guidanceShown = localStorage.getItem('guidance_shown');
    if (guidanceShown) {
      console.log('Guidance was previously shown, hiding guidance');
      setDismissed(true);
    } else {
      console.log('No previous guidance shown, showing guidance');
      setDismissed(false);
    }
  }, [faithPoints, followerCount]);

  const handleDismiss = () => {
    setDismissed(true);
    localStorage.setItem('guidance_shown', 'true');
  };

  if (!showPrayerGuidance && !showFollowerGuidance) {
    return null;
  }

  return (
    <div className="fixed bottom-16 left-1/2 transform -translate-x-1/2 max-w-md w-full z-50">
      <div className="bg-indigo-900 text-white p-4 rounded-lg shadow-lg border-2 border-indigo-500 animate-pulse">
        <div className="flex justify-between items-start">
          <div className="flex items-center">
            <span className="text-2xl mr-2"></span>
            <h3 className="text-lg font-bold">Divine Guidance</h3>
          </div>
          <button
            onClick={handleDismiss}
            className="text-indigo-300 hover:text-white ml-2"
          >
            <span className="text-xl"></span>
          </button>
        </div>

        <div className="mt-2">
          {showPrayerGuidance && (
            <div className="space-y-2">
              <p>
                Welcome, aspiring deity! Begin your divine journey by answering a prayer.
              </p>
              <p className="text-indigo-300 text-sm">
                Click the glowing orb of light above to gather your first Faith Points.
              </p>
            </div>
          )}

          {showFollowerGuidance && (
            <div className="space-y-2">
              <p>
                You've gathered enough faith to attract a follower to your cause!
              </p>
              <p className="text-indigo-300 text-sm">
                Followers will continuously generate Faith Points for you. Visit the Buildings panel to recruit your first disciple.
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default PlayerGuidance;